import React from "react";

import styles from "./styles/note.module.scss";

const Note = (props) => (
	<ul className={styles.container}>
		<li className={styles.note}>
			<a href={props.url} className={styles.note__item}>
				<h2 className={styles.note__title}>{props.title}</h2>
				{props.content.map((item) => (
					<p className={styles.note__content}>{item}</p>
				))}
			</a>
		</li>
	</ul>
);

export default Note;
