import React from "react";

import styles from "./styles/photo-stack.module.scss";

const PhotoStack = (props) => (
	<figure className={styles.stack}>
		<a
			href="https://photos.chirinosky.com"
			target="_blank"
			rel="noopener noreferrer"
		>
			<img className={styles.stack__img} src={props.image} alt=""></img>
			<figcaption className={styles.stack__caption}>
				{props.caption}
			</figcaption>
		</a>
	</figure>
);

export default PhotoStack;
